.container {
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding-top: 0;
    margin-top: 100px;
    width: 100%;
    
    background-color: beige;
    
    
  }
  .container img{
    margin-left: -750px;
  }
  .heading {
    font-weight: bold;
    font-size: 40px;
    font-family: cursive;
    margin-top: -100px;

  }
  
  .toolbar {
    width: 100%;
    display: flex;
    gap: 40px;
    justify-content: space-between;
    align-items: center;
  }
  
  .colors {
    display: flex;
    gap: 20px;
    padding: 0 30px;
    margin-top: 25px;
  }
  
  .colors .color {
    height: 36px;
    width: 36px;
    border-radius: 50%;
    background-color: #239ce2;
  }
  .colors .active {
    border: 2px solid #000;
  }
  
  .toolbar button {
    padding: 8px 16px;
    border-radius: 5px;
    background-color: #239ce2;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: 1px;
    display: flex;
    gap: 5px;
    align-items: center;
    cursor: pointer;
  }
  
  .toolbar button svg {
    height: 20px;
    width: 20px;
  }
  
  .main {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
  }
